/*main body*/
.heroImg{
    margin-top: 18px;
}

.heroImg img{
    width: 238.26px;
    height: 357.28px;
}

.hero{
    margin-top: 40px;
    background: #F9B70B;
    padding: 40px 36px 83px 36px;
}

.hero2{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.heroText{
    font-weight: 500;
    font-size: 26px;
    line-height: 39px;
    text-align: left;
}

