@media (min-width: 1024px){
    /*Nav Bar*/

    .harmBurger{
        display: none;
    }
    .harmBurgerDropDown{
        display: none;
    }

    .navItems{
        display: flex;
        padding-right: 47px;
    }

    .navItems ul{
        display: inline-flex;
        list-style: none;
        gap: 24px;
        align-items: center;
    }

    .navItems ul li{
        font-size: 16px;
        font-weight: 500;
        line-height: 20px;
        cursor: pointer;
    }

    .registerButton{
        background: #F9B70B;
        color: white;
        padding: 10px 37px 10px 37px;
        border-radius: 5px;
        border: none;
        width: 140px;
        height: 40px;
        cursor: pointer;
    }

    .loginButton{
        background: #000000;
        color: white;
        padding: 10px 37px 10px 37px;
        border-radius: 5px;
        border: none;
        width: 140px;
        height: 40px;
        cursor: pointer;
    }

    .navItemActive{
        color: #F9B70B;
    }

/*    footer*/
    footer {
        gap: 122px;
    }

/*    mainbody*/
    .hero{
        padding: 40px 47px 83px 58px;
    }

    .hero2{
        justify-content: space-between;
    }

    .heroText {
        font-weight: 500;
        font-size: 52px;
        line-height: 70px;
    }

    .heroImg img {
        width: 390px;
        height: 586.5px;
    }

}


